import { IArgsMeta, IInletsMeta, IOutletsMeta, IPropsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export class JsParamDescriptor {
    name: string;
    automationRate?: "a-rate" | "k-rate";
    minValue?: number;
    maxValue?: number;
    defaultValue?: number;
}

export default class JsDspProcessor {

    static paramDescriptors: Record<string, JsParamDescriptor> = {};

    static inlets: IInletsMeta = [];
    static outlets: IOutletsMeta = [];
    static args: IArgsMeta = [];
    static props: IPropsMeta = {
        smoothInput: {
            type: "number",
            default: 0.05,
            description: "Linear interpolation coefficient to block-rate input values in seconds"
        }
    };
    static argsOffset: number;

    static get parameterDescriptors() {
        const params = [] as JsParamDescriptor[];
        for (const name in this.paramDescriptors) {
            params.push(this.paramDescriptors[name]);
        }
        return params;
    }

    init(sampleRate: number) {

    }

    process(inputs: Float32Array[][], outputs: Float32Array[][], parameters: Record<string, Float32Array>): boolean {
        return true;
    }

}
