import { Bang, DefaultObject, isBang } from "../../sdk";
import type { IArgsMeta, IInletsMeta, IOutletsMeta, IPropMeta, IPropsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";

export default class Counter extends DefaultObject<{}, {}, [number[] | number], [number, Bang], any[], { maxpoints: number }> {
    static package = "electrosmith";
    static author = "Corvus Prudens";
    static version = "1.0";
    static description = "Generates linear sequences of numbers given a set of points.";
    static inlets: IInletsMeta = [
        {
            isHot: true,
            type: "anything",
            description: "A single number to set the current value, or a list of number pairs to set the sequence"
        },
        {
            isHot: false,
            type: "number",
            description: "The output rate in milliseconds"
        }
    ];
    static outlets: IOutletsMeta = [
        {
            type: "number",
            description: "The current value in the sequence"
        },
        {
            type: "bang",
            description: "Bang when the sequence is complete"
        },
    ];
    static args: IArgsMeta = [
        {
            type: "number",
            description: "The initial value",
            optional: true,
        },
        {
            type: "number",
            description: "The output rate in milliseconds",
            optional: true,
        }
    ];
    static props: IPropsMeta<{ maxpoints: number }> = {
        maxpoints: {
            type: "number",
            default: 32,
            description: "The maximum number of points in the sequence",
        }
    }
    _ = {
        start: 0,
        last_output: 0.0,
        grain: 20.0,
        points: [] as { value: number; duration: number; }[],
        num_points: 0,
        intervalRef: null as number,
        timeoutRef: null as number,
        time: 0.0,
        current_point: 0,
    };

    get_output() {
        const now = performance.now();
        const current_point = this._.points[this._.current_point];

        let progress = current_point.duration === 0
            ? 1.0
            : (now - this._.time) / current_point.duration;

        if (progress > 1.)
            progress = 1.;

        const output = this._.start + (current_point.value - this._.start) * progress;

        return output;
    }

    subscribe() {
        super.subscribe();

        const handleUpdate = () => {
            const current_point = this._.points[this._.current_point];
            const now = performance.now();
            if (now - this._.time >= current_point.duration) {
                this._.current_point++;
                if (this._.current_point >= this._.num_points) {
                    this._.current_point = 0;
                    this.outlet(1, new Bang());
                    this.outlet(0, current_point.value);
                    if (this._.intervalRef) {
                        window.clearInterval(this._.intervalRef);
                        this._.intervalRef = null;
                    }
                    return;
                }
                this._.time = now;
                this._.start = current_point.value;
            }

            const output = this.get_output();

            this.outlet(0, output);
        }

        const handleTimeout = () => {
            handleUpdate();
            this._.intervalRef = window.setInterval(handleUpdate, this._.grain);
        };

        const activateTimer = () => {
            if (this._.timeoutRef) {
                window.clearTimeout(this._.timeoutRef);
                this._.timeoutRef = null;
            }
            if (this._.intervalRef) {
                window.clearInterval(this._.intervalRef);
                this._.intervalRef = null;
            }

            const timeout = this._.grain;
            this._.timeoutRef = window.setTimeout(handleTimeout, timeout);
            this._.time = performance.now();
        };


        this.on("preInit", () => {
            this.inlets = 2;
            this.outlets = 2;

            let maxpoints = this.getProp("maxpoints");
            this._.points = Array.from({ length: maxpoints }, () => ({ value: 0.0, duration: 0.0 }));

            if (this.args.length >= 1) {
                this._.start = this.args[0];
            }

            if (this.args.length >= 2) {
                this._.grain = this.args[1];
                if (this._.grain < 1.0) {
                    this.error("grain must be greater than or equal to 1");
                }
            }
        });
        this.on("propsUpdated", ({ props }) => {
            let maxpoints = this.getProp("maxpoints");
            this._.points = Array.from({ length: maxpoints }, () => ({ value: 0.0, duration: 0.0 }));
        });
        this.on("argsUpdated", ({ args }) => {
            if (args.length >= 1) {
                this._.start = args[0];
            }

            if (args.length >= 2) {
                this._.grain = args[1];
                if (this._.grain < 1.0) {
                    this.error("grain must be greater than or equal to 1");
                }
            }
        });
        this.on("inlet", async ({ data, inlet }) => {
            if (inlet === 0) {
                if (typeof data === 'number') {
                    this._.start = data;
                    if (this._.intervalRef) {
                        window.clearInterval(this._.intervalRef);
                        this._.intervalRef = null;
                    }
                    this.outlet(0, this._.start);
                    return;
                } else if (data.length == 1) {
                    this._.start = data[0];
                    if (this._.intervalRef) {
                        window.clearInterval(this._.intervalRef);
                        this._.intervalRef = null;
                    }
                    this.outlet(0, this._.start);

                    return;
                } else {

                    const input_points = data.length / 2;
                    const max_points = input_points > this._.points.length ? this._.points.length : input_points;

                    for (let i = 0; i < max_points; i++) {
                        const index = i * 2;
                        this._.points[i].value = data[index];
                        this._.points[i].duration = data[index + 1];
                    }

                    this._.num_points = max_points;
                    this._.current_point = 0;
                    activateTimer();
                }
            } else if (inlet === 1) {
                if (typeof data === "number") {
                    this._.grain = data;
                    if (this._.grain < 1.0) {
                        this.error("grain must be greater than or equal to 1");
                    }
                }
            }
        });
        this.on("destroy", () => {
            if (this._.timeoutRef) {
                window.clearTimeout(this._.timeoutRef);
                this._.timeoutRef = null;
            }
            if (this._.intervalRef) {
                window.clearInterval(this._.intervalRef);
                this._.intervalRef = null;
            }
        });
    }
}
