import { Bang, DefaultObject, isBang } from "../../sdk";
import type { IArgsMeta, IInletsMeta, IOutletsMeta, IPropsMeta } from "@jspatcher/jspatcher/src/core/objects/base/AbstractObject";
import { extractFirst } from "@jspatcher/jspatcher/src/core/message";


export default class Metro extends DefaultObject<{}, {}, [boolean, number], [any], [number?], { active: boolean }> {
    static description = "Metronome that outputs bangs according to the specified time";
    static inlets: IInletsMeta = [{
        isHot: true,
        type: "anything",
        description: "Start or stop the metronome"
    }, {
        isHot: false,
        type: "number",
        description: "interval in milliseconds"
    }];
    static outlets: IOutletsMeta = [{
        type: "bang",
        description: "metronomic bangs"
    }];
    static args: IArgsMeta = [{
        type: "number",
        optional: true,
        default: 1,
        description: "Initial interval in milliseconds"
    }];
    static props: IPropsMeta<{ active: boolean }> = {
        active: {
            type: "boolean",
            default: false,
            description: "Globally activate or deactivate the metronome"
        }
    };
    _ = {
        time: +this.args[0],
        active: this.getProp("active"),
        intervalRef: null as number,
        timeoutRef: null as number,
        last: 0
    };
    subscribe() {
        super.subscribe();
        const handleTimeout = () => {
            this._.last = performance.now();
            this.outlet(0, new Bang())
            this._.intervalRef = window.setInterval(() => {
                this._.last = performance.now();
                this.outlet(0, new Bang())
            }, this._.time);
        };
        const activateTimer = (time: number) => {
            if (this._.timeoutRef) {
                window.clearTimeout(this._.timeoutRef);
                this._.timeoutRef = null;
            }
            if (this._.intervalRef) {
                window.clearInterval(this._.intervalRef);
                this._.intervalRef = null;
            }
            if (time && this._.active) {
                const timeout = Math.max(0, this._.last + this._.time - performance.now());
                this._.timeoutRef = window.setTimeout(handleTimeout, timeout);
            }
            this._.time = time;
        }
        this.on("preInit", () => {
            this.inlets = 2;
            this.outlets = 1;
        });
        this.on("postInit", () => {
            activateTimer(+this.args[0]);
        })
        this.on("updateArgs", () => {
            if (typeof this.args[0] === "number") {
                activateTimer(Math.max(0, +this.args[0]));
            }
        });
        this.on("updateProps", () => {
            this._.active = this.getProp("active");
        });
        this.on("inlet", ({ data: rawData, inlet }) => {
            if (inlet === 0) {
                let data = extractFirst(rawData);

                if (isBang(data)) {
                    this._.active = true;
                } else if (typeof data === "number") {
                    if (data > 0)
                        this._.active = true;
                    else
                        this._.active = false;
                }

                activateTimer(this._.time);
            } else if (inlet === 1) {
                let data = extractFirst(rawData);
                activateTimer(Math.max(0, +data));
            }
        });
        this.on("destroy", () => {
            if (this._.timeoutRef) {
                window.clearTimeout(this._.timeoutRef);
                this._.timeoutRef = null;
            }
            if (this._.intervalRef) {
                window.clearInterval(this._.intervalRef);
                this._.intervalRef = null;
            }
        });
    }
}
